import React, { useState } from 'react'
import {
  Card,
  CardHeader,
  CardBody,
  Row,
  Col,
  Form,
  Input,
  FormGroup,
  Label,
  Button
} from 'reactstrap'
import { connect } from 'react-redux'
import { Monitor, Smartphone, Tablet } from 'react-feather'
import '../../../assets/scss/pages/authentication.scss'
import Dashboard from '../../mydashboard/Dashboard'

import ComingSoon from '../misc/ComingSoon'
const LandingPage = () => {
  const [email, setEmail] = useState('')
  const [q, setQ] = useState('')
  const handlingemail = (e) => {
    e.preventDefault()
    setEmail(e.target.value)
  }
  const handlingq = (e) => {
    e.preventDefault()
    setQ(e.target.value)
  }
  return (
    <div>
      {/* <Row>
        <Dashboard />
      </Row> */}
      <Row className="d-flex align-items-center justify-content-center m-0">
        <Col md="7">
          <Card className="mb-0 mt-3">
            <CardHeader className="justify-content-center">
              <h3>We are here to help your dependents</h3>
            </CardHeader>
            <CardBody className="text-center">
              <div className="divider">
                <a href="/#/#/register" className="divider-text">
                  <h5>Register</h5>
                </a>
              </div>
              <Col className="text-center d-flex justify-content-center flex-wrap">
                <h4>You can access your account from any platform</h4>
              </Col>
              <Row className="mt-4">
                <Col className="text-center justify-content-center">
                  {' '}
                  <Smartphone size="35" />
                  <h5>Smartphones</h5>
                </Col>
                <Col className="text-center justify-content-center">
                  {' '}
                  <Monitor size="35" />
                  <h5>PCs</h5>
                </Col>
                <Col className="text-center justify-content-center">
                  {' '}
                  <Tablet size="35" />
                  <h5>Tabs</h5>
                </Col>
              </Row>
              <div className="divider">
                <span className="divider-text">
                  <h5>Query/Feedback</h5>
                </span>
              </div>
              <Form>
                <FormGroup className="form-label-group mt-2">
                  <Input
                    placeholder="Email"
                    className="text-center mb-1"
                    onChange={handlingemail}
                  />
                  <Label>Email</Label>

                  <Input
                    placeholder="Query/Suggestion"
                    className="text-center"
                    onChange={handlingq}
                  />
                  <Label>Query/Suggestion</Label>
                </FormGroup>
              </Form>
              <Button.Ripple
                block
                color={email && q ? 'warning' : 'primary'}
                className="btn-block"
              >
                Submit
              </Button.Ripple>
            </CardBody>
          </Card>
        </Col>
        <Col md="5">
          <ComingSoon />
        </Col>
      </Row>
      <Row></Row>
    </div>
  )
}
const mapStateToProps = (state) => {
  return {
    user: state.auth
  }
}
export default connect(mapStateToProps)(LandingPage)
